	components:{
		
	},
	props: ['msgDatas'],
	computed: {
		i18n () {
		  return this.$t('listModules')  
		},
	},
	filters: {
		ellipsis: function(value) {
			if (!value) return '';
			if (value.length > 8) {
				return value.slice(0, 8) + '...';
			}
			return value;
		},elli: function(value) {
			if (!value) return '';
			if (value.length > 11) {
				return value.slice(0, 11) + '...';
			}
			return value;
		},
		ellipsiss: function(value) {
			if (!value) return '';
			if (value.length > 15) {
				return value.slice(0, 15) + '...';
			}
			return value;
		}
	},
	created() {
		
	},
	mounted() {
		this.arr.push(this.msgDatas);
		// console.log(this.arr)
		if(this.arr.length==0){
			//console.log('加载完成')
		}
		this.arr.map((item,index)=>{
			if(item.isUserFollow==0){
				this.floow='关注';
				this.floowdata=0;
			}else{
				this.floow='已关注';
				this.floowdata=1;
			};
			//点赞
			if(item.isLike==0){
				this.likes=0;
				this.likenum=item.likeCount;
			}else{
				this.likes=1;
				this.likenum=item.likeCount;
			}
			//收藏
			if(item.isFollow==0){
				this.shouc=0;
				this.shoucnum=item.followCount;
			}else{
				this.shouc=1;
				this.shoucnum=item.followCount;
			}
		})
	},
	methods: {
		guanzhu(e){//关注
			if(this.floowdata==0){//未关注
				if(uni.getStorageSync('landRegist')){
					let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
					//console.log(landRegistLG.user.id);
					uni.showLoading({ // 展示loading
						title: '加载中'
					});
					uni.request({
						url: this.api2 + '/rest-rp/follow/user?userId='+landRegistLG.user.id+'&modelId='+e.userId+'&type=4', //接口地址。
						// data: params,
						method: 'GET',
						header: {
							Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
						},
						success: (response) => {
							//console.log(response.data, '---------------response.data--------------');
							if (response.data.ret === '200') {
								uni.hideLoading(); // 隐藏 loading
								this.floow='已关注';
								this.floowdata=1;
							}else if (response.data.ret === '202') {
								uni.removeStorageSync('landRegist');
								uni.removeStorageSync('clickItems');
								this.$store.commit('setHome', 1);
								this.$store.commit('setLandRegist', 0);
								uni.showToast({
									title: '登录已过期，请重新登录',
									icon: 'none',
									duration: 1000
								});
								if (!uni.getStorageSync('landRegist')) {
									this.landRegistra(); // 判断登录状态
								}
							}
						},
						fail: (error) => {
							uni.hideLoading(); // 隐藏 loading
							uni.showToast({
								title: '网络繁忙，请稍后',
								icon: 'none',
								duration: 1000
							});
							//console.log(error, '网络繁忙，请稍后');
						}
					});
				}else if (!uni.getStorageSync('landRegist')) {
					this.landRegistra(); // 判断登录状态
				}
